.contacts {
    background: url(/img/maps.png) 35% 0 $black no-repeat;
    background-size: cover;
    height: 600px;
    width: auto;    
    position: relative;
    z-index: 10;
}
.contacts_wrap {
    padding-top: 250px;
    padding-bottom: 170% 100px;
}

.contacts__form {
    background-color: $white;
    padding: 30px 30px 62px;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba($color: #ffffff, $alpha: 0.6);

    h2 {
        @include title;
        margin-top: 32px;
        margin-bottom: 32px;
        span {
            width: 70px;
        }
    }
}
@media (min-width:400px) {
    .contacts__form {
        h2 {
           span {
            width: 120px;
        } 
        }
        
    }
}

@media (min-width:768px) {
    .contacts__form {
        position: absolute;
        width: 300px;
        height: 300px;
        right: 50px;
        bottom: 100px;
    }
}