.footer {
    background-color: $black;
    position: relative;
    z-index: 10;

}
.footer__wrap {
    padding-top: 50px;
    padding-bottom: 50px;
    align-items: center;
}

.footer__logo {
    margin: 0 auto;
    width: auto;
    text-align: center;
    font-size: px-to-rem(32px);
    line-height: px-to-rem(36px);
    color: $orange;
    font-weight: 700;
    letter-spacing: px-to-rem(1px);
    font-family: 'Lobster', cursive;
}

.footer__menu {
    margin: 0 auto;
    padding: 48px 0;
    list-style: none; 
    width: auto;
    font-size: px-to-rem(18px);
    line-height: px-to-rem(20px);
    text-align: center;
      a {
          text-decoration: none;
          color: $white;
      }
      
}

.footer__item {
    padding-bottom: 20px;
        &:last-child {
          padding-bottom: 0;
        }
        a {
            padding-bottom: 2px;
            &:hover {
            content: "";
            border-bottom: 2px solid $orange;
        }
        }
}

.footer__phone {
    font-size: px-to-rem(18px);
    line-height: px-to-rem(20px);
    text-align: center;
    a {
        text-decoration: none;
    }
    span {
        
    color: $white;
    };
    
}
.footer__small {
    display: block;
    color: $white;
    text-align: center;
    margin-top: 32px;
}