.form {
    margin: 0;
    padding: 0;
    background-color: $grey;
    position: relative;
    z-index: 10;
}

.form__wrap {
    padding-top: 30px;
    padding-bottom: 32px;
    z-index: 10;
}
.form__title {
    @include title;
    
    span {
        width: 55px;
    }
}
.form__text {
    margin-top: 48px;
    margin-bottom: 32px;
    line-height: 24px;
    font-weight: 200;
}
.text {
    font-size: px-to-rem(14px);
    line-height: px-to-rem(16px);
    font-weight: 200;
}

.form-group {
    input[type="text"],
    input[type="email"],
    input[type="tel"] {
        padding: 12px 15px;
        margin-bottom: 25px;
        font-size: px-to-rem(12px);
        line-height: px-to-rem(14px);
        font-weight: 200;
        border: 0;
        border-radius: 3px;
        box-sizing: border-box;
        width: 100%;
    }

}

.form-group__btn {
    @include btn;
    height: 40px;
    max-width: 100%;
}

@media (min-width:380px) {
    .form__title {
        span {
            width: 150px;
        }
    }
}
@media (min-width:480px) {
    .form-group__btn {
        max-width: 100%;
    }
}
@media (min-width:768px) {
    .form__wrap {
        display: flex;
    }
    .form__title-text {
        width: 48%;
        margin: auto 0;
    }
    .form-group {
        width: 45%;
        margin: 32px 0 32px auto;
    }
    .form__title {
        span {
            width: 90px;
        }
    }
    .form-group__btn {
        height: 40px;
        max-width: 100%;
    }
}
@media (min-width:768px) {
    .form__title {
        span {
            width: 150px;
        }
    }

}