.company {
    margin: 0;
    padding: 0;
}

.company__wrap {
    padding-top: 20px;
    margin-bottom: 100px;
    display: block;
    position: relative;
    z-index: 10;
}

.company__title {
    padding-top: 24px;
    @include title
}

.company__item {
    font-size: px-to-rem(21px);
    line-height: px-to-rem(24px);
    font-weight: 500;
    margin-top: 48px;
}

.company__img {
    display: block;
    width: 100%;
    height: auto;
    max-width: 690px;
    margin: 40px auto;
    box-shadow: 0 0 10px rgba($color: #000000, $alpha: 0.4);
}

.company__btn {
    @include btn {
        display: none;
    }
}

@media (min-width:768px) {
    .company__title {
        margin: 48px 16px 24px 16px;
    }
    .company__item {
        max-width: 334px;
        font-size: px-to-rem(16px);
        line-height: px-to-rem(24px);
        padding: 16px auto;
    }
    .company__img {
        display: block;
        position: absolute;
        width: 350px;
        height: auto;
        margin-right: 0;
        right: 0;
        top: 70px;
    }
    .company__btn {
        max-width: 250px;
        height: 50px;
        margin-left: 20px;
        margin-top: 25px;
        vertical-align: bottom;
    }
}
@media (min-width:1024px) {
    .company__title {
        margin: 48px 0 24px ;
        padding-top: 80px;
    }
    .company__img {
        width: 550px;
        height: auto;
    }
    .company__item {
        max-width: 380px;
        margin: 48px 0 32px;
    }
    .company__btn {
        margin-left: 32px;
    }
}