.main-menu {
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    z-index: 99;
    transition: all 0.3s ease 0s;

    &::after {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-color: rgba($color: #000000, $alpha: 0.7);
        opacity: 0;
        z-index: -1;
    }
}
.main-menu__list {
    list-style: none;
    position: relative;
    color: #fff;
    z-index: 100;
    padding-left: 32px;
    padding-right: 32px;
    padding-top: 32px;
    padding-bottom: 0;
    margin: 0;
    transform: translateX(-100%);
    background-color: $orange;
    max-width: 360px;
    height: 100%;
    box-sizing: border-box;
    transition-duration: 500ms;
    transition-property: transform;
    overflow-y: auto;
}

.main-menu__item {
    margin: 20px 0;
}

.main-menu__link, .main-menu__close {
    font-size: px-to-rem(17px);
    line-height: px-to-rem(22px);
    text-decoration: none;
    color: #fff;
    border: none;
    cursor: pointer;

    &:hover {
        text-decoration: underline;
    }
}
.main-menu__close {
    padding-left: 30px;
    background: url(/img/close.svg) left center no-repeat;
}

.main-menu__phone {
    text-decoration: none;
    font-size: px-to-rem(24px);
    line-height: px-to-rem(28px);
    color: $black;    
}

.main-menu__btn {
    @include btn {
        font-size: px-to-rem(17px);
        line-height: px-to-rem(22px);  
    }
    &:hover {
            background-color: $green;
        }
}


.main-menu--active {
    left: 0;
    z-index: 200;
    &::after {
        opacity: 1;
    }
    .main-menu__list {
        transform: translateX(0);
    }
}