.header {
    background: url(/img/header.png) no-repeat center $black;
    color: $black;
    background-size: cover;
    height: 100%;
    position: relative;
    z-index: 10;
    margin: 0;

    &::before {
        background-color: rgba($color: #000000, $alpha: 0.5);
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: -1;
    }
}

.header__wrap {
    padding-top: 5px;
}

.header__subtitle {
    font-size: px-to-rem(21px);
    line-height: px-to-rem(24px);
    padding-top: 25%;
    text-align: center;
    color: $white;
}
.header__title {
    margin: 0 0 100px 0;
    text-align: center;
    color: $white;
    font-size: px-to-rem(40px);
    line-height: px-to-rem(42px);
}

.header__btn {
    @include btn
}
.header__social {
    display: block;
    padding: 50px 0;
    margin: 0 auto;
    width: 100%;
    text-align: center;
    
}

.header__item {
    display: inline-block;
    margin-right: 20px;
    
    
    &:last-child {
        margin-right: 0;
    }
    
    a {
        text-decoration: none;
        color: $white;
    }
    img {
        width: 20px;
        height: 20px;
    }
}

@media (min-width: 768px) {
    .header {
        height: 100vh;
    }

    .header__subtitle {
        padding-top: 100px;
    }
}

@media (min-width: 1024px) {
    .header__subtitle {
        padding-top: 150px;
    }
}
    