.catalog    {
    margin: 0;
    padding: 0;
    background-color: $grey; 
    position: relative; 
    z-index: 10;
}
.catalog__wrap {
    padding-top: 2px;
    padding-bottom: 60px;
}

.catalog__title {
    @include title
}
.catalog__title {
    padding-top: 30px;
}
.catalog__subtitle {
    margin-top: 30px;
    text-align: left;
    font-weight: 600;
    font-size: px-to-rem(24px);
    line-height: px-to-rem(26px);
    color: $white;
    &::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 280px;
        background-color: $green;
        top: 0px;
        left: 0;
        z-index: -1;

    }
}
.catalog__items {
    padding: 0;
    list-style: none;
     li {
        font-size: px-to-rem(14px);
        line-height: px-to-rem(16px);
        border-bottom: 2px solid $orange;
        padding: 10px;
        text-align: center;
        box-sizing: border-box;
        font-weight: 200;
        color: $white;
     }

}

.cards {
    margin-top: 48px;
}

.catalog__card {
    display: block;
    background-color: $white;
    border-radius: 5px; 
    box-shadow: 0 0 10px rgba($color: #000000, $alpha: 0.2);
    text-align: center;
}

.catalog__card-subtitle {
    font-size: px-to-rem(10px);
    line-height: px-to-rem(12px);
    font-weight: 200;
    padding-bottom: 16px;
    margin-bottom: 0;
    text-transform: uppercase;
    letter-spacing: px-to-rem(1px);
}
.catalog__card-title {
    font-size: px-to-rem(24px);
    line-height: px-to-rem(26px);
    font-weight: 400;
    padding-top: 16px;
    margin-bottom: 0;
    font-family: 'Lobster', cursive;
    letter-spacing: px-to-rem(2px);
}
.catalog__card-item {
    padding-right: 25px;
    height: 25px;
    font-size: px-to-rem(14px);
    line-height: px-to-rem(16px);
}
.catalog__img-sale {
    position: relative;
    img {
        max-width: 100%;
        z-index: -5;
    }
    
    p {
        position: absolute;
        bottom: 10px;
        left: 25px;
        margin: 0;
        width: 50px;
        font-size: px-to-rem(16px);
        line-height: px-to-rem(18px);
        font-weight: 500;
        color: $white;
        z-index: 2;
        text-align: center;
            &::before {
                content: "";
                position: absolute;
                bottom: -25px;
                left: -15px;
                background-color: $orange;
                width: 80px;
                height: 80px;
                border-radius: 100%;
                z-index: -1;
            }
        
    }
}
.rub {    
    font-size: px-to-rem(12px);
    line-height: px-to-rem(16px);
}
.catalog__img {
    overflow: hidden;
    width: 80%;
    height: 210px;
    border-radius: 3px;
    margin: auto;
    
    img {
        height: 99%;
        box-shadow: 0px 0px 7px rgba($color: #000000, $alpha: 0.9);
    }
}
.catalog__btn {
    width: 80%;
    margin: 0 auto;
    padding-top: 24px;
    padding-bottom: 24px;
}
.catalog__btn button {
    @include btn {
        height: 50px;
    }
}
/*.rub { 
    line-height: 5px;
    width: 0.4em;
    border-bottom: 1px solid #000; 
    display: inline-block;
}*/

@media (min-width:550px) {
    
    .catalog__card {
        max-width: 100%;
    }
    .cards {
         display: grid;
         grid-template-columns: 1fr 1fr 1fr;
         grid-column-gap: 20px;
         grid-row-gap: 20px;
    }
}

@media (min-width:768px) {
    
    .catalog__items {
        padding: 24px 0 0 0;
        display: grid;
         grid-template-columns: 1fr 1fr 1fr 1fr;
         grid-column-gap: 50px;
         list-style: none;
         li {
             border-radius: 5px;
             padding: auto 5px;
             text-align: center;
             border: 2px solid $orange;
         }

    }
    .catalog__subtitle {
        &::before {
            height: 180px;
        }
    }
}
@media (min-width:1024px) {
    .cards {
        grid-template-columns: 1fr 1fr 1fr 1fr;
    }
}