@mixin clearfix() {
  &:before,
  &:after {
    content: '';
    display: table;
    clear: both;
  }
}

@mixin min-max($min-width, $max-width, $padding) {
  min-width: $min-width;
  max-width: $max-width;
  padding: 0 $padding;
  box-sizing: border-box;
  margin: 0 auto;
}

@mixin adaptive-video() {
  position: relative;
  padding-bottom: 56.25%; /* задаёт высоту контейнера для 16:9 (если 4:3 — поставьте 75%) */
  height: 0;
  overflow: hidden;

  iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

}

@mixin visually-hidden() {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0,0,0,0);
  overflow: hidden;
}

@function px-to-rem($size, $basicSize: 16px) {
  @return $size / $basicSize#{rem};
}

@mixin dynamicText($minSizePercent, $maxSize, $minSize, $minWidthPx, $maxWidth, $minWidth) {
  font-size: calc(#{$minSizePercent} + (#{$maxSize} - #{$minSize}) * (100vw - #{$minWidthPx}) / (#{$maxWidth} - #{$minWidth}));
}

@mixin btn {
  width: 100%;
  height: 70px;
  background-color: $green;
  font-size: px-to-rem(14px);
  line-height: px-to-rem(16px);
  text-align: center;
  color: $white;
  border-radius: 3px;
  cursor: pointer;
  border: none;
  display: block;
  padding: auto;
  text-decoration: none;
  text-transform: uppercase;
  transition-property: background-color;
  transition-duration: 500ms;
  box-shadow: 0 0 10px rgba($color: #000000, $alpha: 0.4);
  z-index: 1;
  
  @content;

  &:hover {
    background-color: #0698FF;
  }
  @media (min-width:480px) {
    max-width: 370px;
    margin: 0 auto;
  }

}

@mixin title {
    display: block;
    margin-top: 45px;
    font-size: px-to-rem(32px);
    line-height: px-to-rem(36px);
    font-weight: 900;
