.fixed {
    width: 100%;
    height: auto;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    position: fixed;
    z-index: 100;
    box-sizing: border-box;
    background-color: rgba($color: #000000, $alpha: 0.8);
    color: $white;
    
}

.fixed__wrap {
    display: flex;
    align-items: center;
    margin: 15px auto;
    
}

.fixed__humburger {
    flex-basis: 20px;
    flex-shrink: 0;
    margin-right: 10%;
    span {
        display: block;
        background-color: $white;
        width: 100%;
        height: 2px;
        border-radius: 2px;
        margin-bottom: 4px;
    }
    &:hover {
        span {
            &:nth-child(2) {
                width: 75%;
                margin-left: auto;
            }
            &:last-child {
                width: 50%;
                margin-left: auto;
            }
        }
    }
}
.fixed__logo {
        margin: 0 auto;
        width: auto;
        text-align: center;
        text-decoration: none;
        font-size: px-to-rem(20px);
        line-height: px-to-rem(22px);
        color: $orange;
        font-weight: 700;
        letter-spacing: px-to-rem(1px);
        font-family: 'Lobster', cursive; 

        &:hover {
            color: $green;
    }
    a {
        text-align: center;
        text-decoration: none;
        font-size: px-to-rem(20px);
        line-height: px-to-rem(22px);
        color: $orange;
        font-weight: 700;
        letter-spacing: px-to-rem(1px);
        font-family: 'Lobster', cursive; 

        &:hover {
            color: $green;
    }
    }
    
}
.fixed__menu {
    display: none;
    margin: 0;
    padding: 0;
      list-style: none; 
      width: auto;
      font-size: px-to-rem(14px);
      line-height: px-to-rem(16px);

      a {
          text-decoration: none;
          color: $white;
      }
    }

.fixed__item {
    display: inline-block;
    margin-right: 25px;
    a {
        padding-top: 2px;
        &:hover {
        content: "";
        border-top: 2px solid $orange;
    }
    }

    &:last-child {
        margin-right: 0;
    }

    
    
}

.fixed__phone {
    margin-left: 10%;
    
    a {
        text-decoration: none;
        color: $white;
        
    }
    img {
        display: block;
        width: 20px;
        height: 20px;
    }
    span {
        display: none;
        font-size: px-to-rem(16px);
        line-height: px-to-rem(22px);
    }
}

@media (min-width: 768px){
    .fixed__logo {
        font-size: px-to-rem(24px);
        line-height: px-to-rem(28px);
    }
    .fixed__humburger {
        margin-right: (20%);
    }
    .fixed__phone {
        img {
            display: none;
        }
        span {
            display: block;
        }
    }
}
    

@media (min-width: 1024px) {
    .fixed__humburger {
        display: none;
    }
    .fixed__logo {
        margin-left: 0;
        text-align: left;
        margin-right: 24px;
    }
    .fixed__menu {
        display: block;
        margin: 0 auto;
    }
    
}