.numbers {
    margin: 0;
    padding: 0;
    background-color: $green;
}
.numbers__wrap  {
    padding-top: 20px;
    padding-bottom: 20px;
}

.numbers__title {
    @include title;
    span {
        width: 70px;
    } 
}
.numbers__title {
    color: $white;
}
.numbers__block {
    padding: 0;
    margin-bottom: 0;
    color: $white;
}
.numbers__item {
    max-width: 100%;
    list-style: none;
    position: relative;
    margin-bottom: 32px;
    p {
        margin-left: 150px;
        text-align: center;
        &:first-child {
            font-size: px-to-rem(42px);
            font-weight: 600;
            margin-bottom: 0;
        }
    }
    .item {
        font-size: px-to-rem(16px);
        font-weight: 200;
    }
    img {
        position: absolute;
        left: 0;
        top: 10px;
    }
}

@media (min-width:550px) {
    .numbers__title {
        span {
            width: 150px;
        }
    }
    .numbers__block {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 20px;
        grid-row-gap: 20px;
    }
    .numbers__item {
        p {
            margin-left: 90px;
        }
        img {
            width: 60px;    
            height: auto;
            top: 50px;
        }
    }
}

@media (min-width:768px) {
    .numbers__item {
        p {
            margin-left: 120px;
        }
}
}
@media (min-width:1024px) {
    .numbers__item {
        p {
            margin-left: 50px;
            margin-bottom: 0;
        }
    }
    .numbers__block {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-column-gap: 20px;
        grid-row-gap: 20px;
    }
}